/*!
 *  
 *  vuex F5
 *  window.localStorage 
 */

import Vue from 'vue';
import Vuex from 'vuex';
import {getAllDeptId} from '@/api/getData';
Vue.use(Vuex);
export const baseStore = new Vuex.Store({
  state: {
      userInfo:{},
      currentSelectMenu:{},//
      allowAccessMenu:{},//
      allCompanyInfo:[],//
      windowname:''//name,,window.namename,
  },
  mutations: {
    setUserInfo(state, payload) {//statestatepayload
        window.localStorage.setItem("userInfo", JSON.stringify(payload));
        state.userInfo = payload;
    },
    dropUserInfo(state) {//
        window.localStorage.removeItem("userInfo");
        state.userInfo = {};
    },
    setCurrentSelectMenu(state, payload) {//statestatepayload
        window.localStorage.setItem("currentSelectMenu", JSON.stringify(payload));
        state.currentSelectMenu = payload;
    },
    setAllCompanyInfo(state, payload){
        state.allCompanyInfo = payload;
    },
    setWindowname(state, payload){
        state.windowname = payload;
        window.localStorage.setItem("windowname", payload);
    }
  },
  actions:{ // 
    async getAllDeptId({commit}) {
        try {
          const res = await getAllDeptId()
          if (res.resultCode == '0') {
            commit('setAllCompanyInfo', res.result);
          } else {
            throw new Error(res)
          }
        } catch (err) {
          console.log('')
        }
      },
  },
  getters: {
    getUserInfo: state => {
        if(state.userInfo == undefined || state.userInfo.username == undefined){
            var userInfo = window.localStorage.getItem("userInfo");
            if(userInfo == undefined){
                return new Object();
            }else{
                state.userInfo=JSON.parse(userInfo);
                return state.userInfo;
            }
        }else{
            return state.userInfo
        }
      
    },

    getCurrentSelectMenu:state =>{
        if(state.currentSelectMenu != undefined && state.currentSelectMenu.firstDegreeMenu !=undefined){
            return state.currentSelectMenu;
        }else{
            var currentSelectMenu = window.localStorage.getItem("currentSelectMenu");
            if(currentSelectMenu == undefined){
                return new Object();
            }else{
                state.currentSelectMenu=JSON.parse(currentSelectMenu);
                return state.currentSelectMenu;
            }
        }
    },

    getAllCompanyInfo:state =>{
        return state.allCompanyInfo;
    },

    getWindowname:state =>{
        if(state.windowname == undefined || state.windowname ==''){
            return '-1';
        }else{
            return state.windowname;
        }
        
    },

  }
});



